import './css/app.css';

import Helper from './common/helper';
window.Helper = Helper;

import Vue from 'vue';
Vue.config.productionTip = false;


import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
Vue.component('loading', Loading);


window.Bus = new Vue({});

import { API_URL } from './common/config';
import JwtService from './services/jwt';

window.axios = require('axios');
window.axios.defaults.baseURL = API_URL;
window.axios.defaults.headers.common['X-Requested-With'] = 'XMLHttpRequest';

let token = JwtService.getToken();
if(token) window.axios.defaults.headers.common.authorization = `Bearer ${token}`;


import './plugins/vuetify';
import './plugins/validate';
import './plugins/googleAuth';



import router from './router';
import store from './store';

import { CHECK_AUTH, REFRESH_TOKEN } from './store/actions.type';

router.beforeEach((to, from, next) => {	
	store.dispatch(CHECK_AUTH).then((user) => {		
		let needAuth = to.meta.type === 'user';
		let guestOnly = to.meta.type === 'guest';
		let forAll = to.meta.type === 'all';

		if(user){  
			//有token
			let tokenStatus = JwtService.tokenStatus();
			
			if(tokenStatus === -1) {
				//token過期
				if(forAll) return next();
				else {
					store.dispatch(REFRESH_TOKEN).then(token => {	
						if(token){
							if(guestOnly) return next({ name: 'home'});
							else return next();
						}else{
							//REFRESH_TOKEN 失敗
							if(guestOnly) return next();
							else return next({ path: '/login' , query:{ returnUrl: to.path }});
						}
					})	
				}
			}else if(tokenStatus === 0) {
				//token 即將到期
				store.dispatch(REFRESH_TOKEN).then(token => {	
					if(token){
						if(guestOnly) return next({ name: 'home'});
						else return next();
					}else{
						//REFRESH_TOKEN 失敗
						if(needAuth) return  next({ path: '/login' , query:{ returnUrl: to.path }});
						else return next();
					}
				})	

				return next();
			}else {
				//token正常
				if(guestOnly) return next({ name: 'home'});
				else return next();
			} 	
			
		}else{
			//沒有token
			if(needAuth){
				//需要驗證, 導入login
				return next({ path: '/login' , query:{ returnUrl: to.path }});
			}
			else{
				return next();
			} 
		}
	})
});



import App from './App.vue';

new Vue({
	router,
	store,
	render: h => h(App),
}).$mount('#app')
